@charset "UTF-8";

/// Default breakpoints.
///
/// @group flexed
///
/// @type map
///
/// @property {thin} thin [36em]
///   Thinnest breakpoint setting for breakpoint mixin.
///
/// @property {wide} wide [46em]
///   Medium breakpoint setting for breakpoint mixin.
///
/// @property {thin} full [60em]
///   Widest breakpoint setting for breakpoint mixin.
///
/// @access public
$breakpoints: ( thin: 36em, wide: 46em, full: 60em) !default;


/// Default max-width for container mixin.
///
/// @group flexed
///
/// @type string
///
/// @access public
$row-width: 100% !default;


/// Default gutter converted to percentage for column mixin.
///
/// @group flexed
///
/// @type unitless
///
/// @access public
$gutter-width:1 !default;


/// Mixin to create a grid container(row) based on flexbox.
/// This mixin essentially creates a flexbox grid row to contain one or more flexbox grid columns
///
/// @group flexed
///
/// @param {Width} $max-row-width [100%] - Element's width
/// @param {Boolean} $center [false] - true / false
///
/// @example scss - Example
///   .foo {
///     @include conatiner(1200px);
///   }
/// @example scss - Output
///   .foo {
///     margin: 0 auto;
///     max-width: 1200px;
///     display: flex;
///     flex-wrap: wrap;
///     justify-content: space-between;
///   }
@mixin container ($max-row-width: $row-width, $center: false ) {
    margin: 0 auto;
    max-width: $max-row-width;
    display: flex;
    flex-wrap: wrap;
    @if $center == true {
        justify-content: center;
    } @else {
        justify-content: space-between;
    }
}


/// Mixin to create a grid column(row) based on flexbox.
/// This mixin creates a flexbox grid column
///
/// @group flexed
///
/// @param {Width} $width [1] - Column width converted to percentage
/// @param {Width} $gutter [1] - Gutter width
///
/// @example scss - Example
///   .bar {
///     @include column(1/2);
///   }
/// @example scss - Output
///   .bar {
///     width: 49.5%;
///   }
@mixin column ($width:1, $gutter: $gutter-width) {
    $percentage: percentage($width);
    width: $percentage - ($gutter - $width * $gutter);
}


/// Media query Mixin for map based breakpoint.
/// This mixin creates a media query block
///
/// @group flexed
///
/// @param {Name} $name - thin(36em) OR wide(46em) OR full(60em)
///
/// @example scss - Example
///   @include breakpoint(full) {
///     .baz { color: black; }
///   }
/// @example scss - Output
///   @media (min-width: 60em) {
///     .baz { color: black; }
///   }
@mixin breakpoint($name) {
    @if not map-has-key($breakpoints, $name) {
        @warn "Warning: `#{$name}` is not a valid breakpoint name.";
    }
    @else {
        @media (min-width: map-get($breakpoints, $name)) {
            @content;
        }
    }
}

//Border-box everything.
*, *:before, *:after { box-sizing: border-box; }
